<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex sm12 md9 lg8>
        <v-toolbar dark color="primary">
          <v-icon class="mr-2" @click="$router.go(-1)"
            >mdi-arrow-left-thick</v-icon
          >
          <v-toolbar-title>ลืมรหัสผ่าน</v-toolbar-title>
        </v-toolbar>

        <v-container class="grey lighten-5">
          <v-row no-gutters>
            <v-col cols="12" sm="12" md="5">
              <v-card>
                <v-img
                  class="white--text"
                  height="300px"
                  :src="require('../assets/img/home/login-logo.jpeg')"
                >
                </v-img>
              </v-card>
            </v-col>

            <v-col cols="12" md="6" class="mx-auto my-auto">
              <v-row class="mx-auto mt-3">
                <h3>กรอกข้อมูลเพื่อส่งรหัสผ่านไปยัง Email ของท่าน</h3>
              </v-row>
              <v-card-text>
                <v-form v-model="isFormValid">
                  <v-text-field
                    outlined
                    dense
                    prepend-icon="mdi-account"
                    name="Username"
                    label="Username"
                    type="text"
                    v-model="username"
                    :rules="usernameRules"
                  ></v-text-field>
                  <v-text-field
                    outlined
                    dense
                    prepend-icon="mdi-email"
                    name="Email"
                    label="Email"
                    type="email"
                    v-model="email"
                    :rules="emailRules"
                  ></v-text-field>
                  <v-radio-group class="mt-0" v-model="radioGroup" row>
                    <v-radio label="ผู้ซื้อ" value="buyer"></v-radio>
                    <v-radio label="ผู้ขาย" value="seller"></v-radio>
                  </v-radio-group>
                  <v-btn
                    block
                    class="mr-1"
                    color="primary"
                    :disabled="!isFormValid"
                    @click.prevent="checkSubmit"
                  >
                    ยืนยัน
                  </v-btn>
                </v-form>
              </v-card-text>
            </v-col>
          </v-row>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    radioGroup: "",
    isFormValid: false,
    email: "",
    username: "",
    usernameRules: [v => !!v || "กรุณากรอก Username"],
    emailRules: [
      v => !!v || "กรุณากรอก Email",
      v => /.+@.+\..+/.test(v) || "Email ต้องอยู่ในรูปแบบที่ถูกต้อง"
    ]
  }),
  methods: {
    checkSubmit() {
      if (this.radioGroup === "") {
        this.$swal.fire({
          icon: "info",
          title: "กรุณาเลือกประเภทบัญชี",
          confirmButtonText: "ยืนยัน"
        });
        return;
      }

      var formData = new FormData();
      formData.append("username", this.username);
      formData.append("email", this.email);
      formData.append("type", this.radioGroup);
      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/admin/forgetEmail.php",
          formData
        )
        .then(res => {
          console.log(res);
        });
    }
  },
  mounted() {
    window.scroll(0, 0);
  }
};
</script>
<style scoped>
#rounded {
  border-radius: 5px;
}
</style>



